import styles from "./profile-personal.module.css";
import React, { FormEvent, useEffect, useState } from "react";
import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { useFormAndValidation } from "../../hooks/useFormAndValidation";
import { useAppSelector } from "../../hooks/redux";
import { getCookie } from "../../utils/cookie";
import { usePatchUserDataMutation } from "../../store/api";
import { useActions } from "../../hooks/actions";

const ProfilePersonal = () => {
  const { name, email, password } = useAppSelector((state) => state.auth);
  const [isChanged, setChanged] = useState<boolean>(false);
  const token = document.cookie ? getCookie("token") : "";
  const [patchUserData] = usePatchUserDataMutation();
  const { refreshUser } = useActions();

  const { values, handleChange, errors, isValid, resetForm } =
    useFormAndValidation({
      email: email,
      password: password,
      name: name,
    });

  useEffect(() => {
    if (
      email === values.email &&
      password === values.password &&
      name === values.name
    ) {
      setChanged(false);
    } else setChanged(true);
  }, [values, name, email, password, isChanged]);

  const submitForm = (event: FormEvent) => {
    event.preventDefault();
    const userData = {
      token,
      ...values,
    };
    patchUserData(userData)
      .then(() => {
        refreshUser(values);
      })
      .catch((err) => console.log(err));
  };

  return (
    <div>
      <form onSubmit={(event) => submitForm(event)}>
        <Input
          type={"text"}
          placeholder={""}
          onChange={(e) => handleChange(e)}
          value={values.name || ""}
          name={"name"}
          error={!isValid}
          errorText={errors.name || ""}
          extraClass="mb-6"
        />
        <Input
          type={"email"}
          placeholder={""}
          onChange={(e) => handleChange(e)}
          value={values.email || ""}
          name={"email"}
          error={!isValid}
          errorText={errors.email || ""}
          extraClass="mb-6"
        />
        <Input
          type={"password"}
          placeholder={""}
          onChange={(e) => handleChange(e)}
          value={values.password || ""}
          name={"password"}
          error={!isValid}
          errorText={errors.password || ""}
          extraClass={"mb-6"}
          autoComplete={"off"}
        />
        <div
          className={`${styles.button_container} ${
            !isChanged ? styles.hidden : ""
          }`}
        >
          <Button
            type="primary"
            htmlType="button"
            onClick={() => resetForm({ name, email, password })}
          >
            
          </Button>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </div>
      </form>
    </div>
  );
};

export default ProfilePersonal;
